@charset "utf-8";
/* CSS Document */

/* -----------------------------------------------
:: Reset
-------------------------------------------------- */
meta.foundation-data-attribute-namespace {
  font-family: false; /* Initialise Foundation 5.5.1 JS for modals, column equalizer, #mobile-main-menu .top-bar (used as the mobile menu between 320px-768px), #primary .f-dropdown (used beyond 768px) */
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  width: 100% !important;
  overflow-x: hidden; /* Prevent horizontal scroll on devices */
}

html {
  height: 100% !important;
  -webkit-text-size-adjust: none; /* Prevent font scaling in landscape */
}

html,
body,
div,
span,
object,
iframe,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img,
object,
embed {
  max-width: 100%;
}

object,
embed {
  height: 100%;
}

img {
  display: inline-block;
  vertical-align: middle;
  -ms-interpolation-mode: bicubic;
}

button,
.button {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  line-height: 1;
  text-align: inherit;
  text-decoration: none;
  background: none;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  transition: none;
  cursor: pointer;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

button:hover,
button:focus,
.button:hover,
.button:focus {
  color: transparent;
  background-color: transparent;
  outline: none;
}

/* -----------------------------------------------
:: Float clearing
-------------------------------------------------- */
.cf:after,
.row:after {
  clear: both;
  content: ".";
  display: block;
  height: 0;
  visibility: hidden;
  font-size: 0;
}

