/* -----------------------------------------------
:: Element control - general
-------------------------------------------------- */
.center {
  margin: 0 auto 0 auto;
  text-align: center;
}

.spacer {
  padding-bottom: 1em;
}

.large-spacer {
  padding-bottom: 2em;
}

.xlarge-spacer {
  padding-bottom: 3em;
}

.overflow {
  overflow: hidden;
}

.left {
  float: left !important;
}

.right {
  float: right !important;
}

.hide {
  display: none;
}

.full-width-background {
  margin: 0 -100%;
  padding: 1em 100%;
  background: #f7f5f2;
}

/* -----------------------------------------------
:: Element control - inline-list
-------------------------------------------------- */
.inline-list {
  margin: 0 auto;
  list-style: none;
  overflow: hidden;
}

.inline-list > li {
  float: left;
}

.inline-list > li > * {
  display: block;
}

/* -----------------------------------------------
:: Element control - animation
-------------------------------------------------- */
/* hue-wipe */
@-webkit-keyframes hue-wipe {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes hue-wipe {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@-o-keyframes hue-wipe {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes hue-wipe {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

/* floating */
@-webkit-keyframes floating {
  from {
    -webkit-transform: translate(0, 0px);
  }
  65% {
    -webkit-transform: translate(0, 15px);
  }
  to {
    -webkit-transform: translate(0, -0px);
  }
}
@-moz-keyframes floating {
  from {
    -moz-transform: translate(0, 0px);
  }
  65% {
    -moz-transform: translate(0, 15px);
  }
  to {
    -moz-transform: translate(0, -0px);
  }
}
@-o-keyframes floating {
  from {
    -o-transform: translate(0, 0px);
  }
  65% {
    -o-transform: translate(0, 15px);
  }
  to {
    -o-transform: translate(0, -0px);
  }
}
@keyframes floating {
  from {
    transform: translate(0, 0px);
  }
  65% {
    transform: translate(0, 15px);
  }
  to {
    transform: translate(0, -0px);
  }
}

/* wobble */
@-webkit-keyframes wobble {
  15% {
    -webkit-transform: translateY(5px);
  }
  30% {
    -webkit-transform: translateY(-5px);
  }
  50% {
    -webkit-transform: translateY(3px);
  }
  65% {
    -webkit-transform: translateY(-3px);
  }
  80% {
    -webkit-transform: translateY(2px);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}
@-moz-keyframes wobble {
  15% {
    -moz-transform: translateY(5px);
  }
  30% {
    -moz-transform: translateY(-5px);
  }
  50% {
    -moz-transform: translateY(3px);
  }
  65% {
    -moz-transform: translateY(-3px);
  }
  80% {
    -moz-transform: translateY(2px);
  }
  100% {
    -moz-transform: translateY(0);
  }
}
@-o-keyframes wobble {
  15% {
    -o-transform: translateY(5px);
  }
  30% {
    -o-transform: translateY(-5px);
  }
  50% {
    -o-transform: translateY(3px);
  }
  65% {
    -o-transform: translateY(-3px);
  }
  80% {
    -o-transform: translateY(2px);
  }
  100% {
    -o-transform: translateY(0);
  }
}
@keyframes wobble {
  15% {
    transform: translateY(5px);
  }
  30% {
    transform: translateY(-5px);
  }
  50% {
    transform: translateY(3px);
  }
  65% {
    transform: translateY(-3px);
  }
  80% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0);
  }
}

/* -----------------------------------------------
:: SVG styles
-------------------------------------------------- */
svg,
.svg {
  width: inherit;
  height: inherit;
}
.svg-hover * {
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}

.hexagon {
  width: 150px;
  height: 150px;
}
.hexagon polygon {
  stroke: #d99c05;
  fill: #eaa806;
} /* fill colour added for cross-broswer compatibility - running an opacity animation over an SVG gradient image doesn't look great in IE and Firefox */
.hexagon.hue-wipe polygon {
  stroke: #fff;
  fill: none;
}
.hexagon.hue-wipe svg > polygon:nth-of-type(3) {
  -webkit-animation: hue-wipe 0.9s linear 0s infinite alternate;
  -moz-animation: hue-wipe 0.9s linear 0s infinite alternate;
  -o-animation: hue-wipe 0.9s linear 0s infinite alternate;
  animation: hue-wipe 0.9s linear 0s infinite alternate;
}
.hexagon.hue-wipe svg > polygon:nth-of-type(2) {
  -webkit-animation: hue-wipe 0.9s linear 0.3s infinite alternate;
  -moz-animation: hue-wipe 0.9s linear 0.3s infinite alternate;
  -o-animation: hue-wipe 0.9s linear 0.3s infinite alternate;
  animation: hue-wipe 0.9s linear 0.3s infinite alternate;
}
.hexagon.hue-wipe svg > polygon:nth-of-type(1) {
  -webkit-animation: hue-wipe 0.9s linear 0.6s infinite alternate;
  -moz-animation: hue-wipe 0.9s linear 0.6s infinite alternate;
  -o-animation: hue-wipe 0.9s linear 0.6s infinite alternate;
  animation: hue-wipe 0.9s linear 0.6s infinite alternate;
}

.bee-character {
  width: 100px;
  height: 146px;
}
.tile-instance > svg {
  width: auto;
  height: auto;
}
.tile-instance polygon {
  stroke: #b3b300;
  fill: #ffff00;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
.tile-instance.wobble polygon {
  stroke: #0542d9;
  fill: #316bfa;
}
.tile-instance.disabled polygon {
  stroke: #b30000;
  fill: #ff0000;
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
}

/* -----------------------------------------------
:: General Buttons
-------------------------------------------------- */
button.rounded {
  padding: 0.5em 2em 0.5em 1em;
  font-weight: 700;
  font-size: 16px;
  font-size: 1rem;
  color: #737370;
  background-color: #e3e4e2;
  border: 1px solid #737370;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
  border-radius: 50px;
}

button.rounded:after {
  content: url("../images/icons/svg/right-thin-chevron.svg");
  position: absolute;
  right: 16px;
  width: 8px;
  height: 16px;
  line-height: 0;
}

/* -----------------------------------------------
:: Main
-------------------------------------------------- */
main {
  position: relative;
  display: block; /* IE hack */
}

main > * {
  max-width: 64em; /* 1024px; */
  margin: 0 auto;
  background: #316bfa; /* For browsers that do not support gradients */
  background: -webkit-radial-gradient(circle, #fff, #316bfa); /* Safari */
  background: -o-radial-gradient(
    circle,
    #fff,
    #316bfa
  ); /* Opera 11.6 to 12.0 */
  background: -moz-radial-gradient(
    circle,
    #fff,
    #316bfa
  ); /* Firefox 3.6 to 15 */
  background: radial-gradient(circle, #fff, #316bfa); /* Standard syntax */
  border: 0.5em solid #fac031;
}

main section.page > .row > .columns {
  padding-left: 0.5em;
  padding-right: 0.5em;
}

main h1 {
  text-align: center;
}

/* -----------------------------------------------
:: 	Grid
----------------------------------------------- */
.row:before,
.row .row:after {
  content: " ";
  display: table;
}

.row {
  width: 100%;
  min-width: 17em; /* 320px-16px-32px border; */
  max-width: 64em; /* 1024px; */
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  *zoom: 1;
}

.row .row {
  width: auto;
  min-width: 0;
  max-width: none;
  margin-left: -1em;
  margin-right: -1em;
}

.column,
.columns {
  position: relative;
  float: left;
  width: 100%;
  padding-left: 1em;
  padding-right: 1em;
}

